export const getDaysBetweenDates = (dates, weekendCounting = false) => {
  if (!dates || !dates[1]) return 0;

  const startDate = new Date(dates[0]);
  const endDate = new Date(dates[1]);

  startDate.setHours(0, 0, 0, 0);
  endDate.setHours(23, 59, 59, 999);

  const oneDay = 1000 * 3600 * 24;
  let daysDiff = Math.ceil((endDate.getTime() - startDate.getTime()) / oneDay);

  if (weekendCounting) {
    const startDay = startDate.getDay();
    const endDay = endDate.getDay();

    const weekends = [0, 6];

    if (weekends.includes(startDay)) daysDiff--;
    if (weekends.includes(endDay)) daysDiff--;

    const fullWeeks = Math.floor(daysDiff / 7);
    const weekendDays = fullWeeks * 2;

    if (startDay === 6) daysDiff--;
    if (endDay === 0) daysDiff--;

    daysDiff -= weekendDays;
  }

  if (daysDiff < 0) daysDiff = 0;

  return daysDiff;
};
