import { useState } from "react";
import { AddLimit as UI } from "./ui/AddLimit";
import { api, prepareEntityToSendById } from "../../../../../../../shared/api";
import { toastMessageConfig } from "../../../../../../../entities/toastMessageConfig";

export const AddLimit = ({i18n,toastRef,dialogState,employee,vacationTypes}) => {
  const [componentStatus, setComponentStatus] = useState({ status: "waiting" });

  const { t } = i18n

  const [dialogStateValue, setDialogStateValue] = dialogState;


  const onSubmit = (employee) => async (fData) => {

    setComponentStatus({ status: "loading" });

    fData.Employee =employee
    prepareEntityToSendById(fData,"Employee")

    const postLimiteResponse = await api.vacationLimits.post(fData)

    setDialogStateValue({visible:false})

    const {warning,error} = postLimiteResponse

    if(warning){
      toastRef.current.show({ severity: 'warn', summary: warning.title, detail: warning.message,life:toastMessageConfig.life})
      return
    }

    if(error){
      toastRef.current.show({ severity: 'error', summary: error.title, detail: error.message,sticky:true })
      return
    }

    toastRef.current.show({ severity: 'success', summary: t("repeatedWords.success"), detail: t("message.successfulOperation.message"),life:toastMessageConfig.life})
    
  };

 return <UI i18n={i18n} onSubmit={onSubmit(employee)} vacationTypes={vacationTypes} {...(componentStatus.status === "loading"?{loading:true}:{})}/>;
  
};
